import {Component,  Input, OnInit} from '@angular/core';
import {Message} from "../../../../../Classes/Classes";
import {DatabaseService} from "../../../../../services/database.service";
import {AppContextService} from "../../../../../services/app-context.service";

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnInit {
  
    public buttons = [];
    public contactName;
    public svgIcon;
    @Input() public context : Message ;
 
  constructor(
      public appContext : AppContextService,
      public database : DatabaseService,
  ) { }
  
  async onErrorContactImg(event){
	//Если контакт пользователя сам поменял изображение, то ссылка на него меняется.
	//Вылетает ошибка загрузкти ресурса, поэтому перегружаем ресурс из объекта User
	await this.database.onErrorContactImg(event, this.context);
	return true;
    }

  ngOnInit() {

      this.contactName = this.context.sender.name || this.context.sender.displayName || this.context.sender.phoneNumber || this.context.sender.email;
	  this.svgIcon = this.context.sender.uid === this.appContext.appUser.uid ? 'outgoing' : (this.context.acton === 'accepted' ? 'accepted' : 'missing');
      }

}
